import * as React from 'react'
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, ButtonGroup, Button, Row, Col, Nav, Navbar, Dropdown, Badge, Modal, Form } from 'react-bootstrap';
import CoffeeCard from '../../components/CoffeeCard';
import CSData from "../../content/cs-2019.json"
import ITData from "../../content/it-2019.json"
import { useLocalStorage } from 'react-use';

const dummyPrice = 59

function PosPage() {

    

    //window.localStorage['coffee'] = 'Latte'
    const [coffee, setCoffee] = useLocalStorage('coffee', 'Latte')

    let [coffeeTitles, setCoffeeTitles] = React.useState([])
    let [subMenu, setSubMenu] = React.useState('iced')
    let [cart, setCart] = useLocalStorage('cart',[])

    function addToCard(coffee) {
        // console.debug(coffee)
        cart.push(coffee)
        console.table(cart)
        setCart([...cart])
    }

    /* React.useEffect(()=>{
        let data = window.localStorage['coffee']
        setLocalData(data)
    },[]) */

    React.useEffect(() => {
            let items = []
            //Run once after the page finished loading
            //Fetch from https:// api.sampleapis.com/coffee/hot
            fetch(`https://api.sampleapis.com/coffee/${subMenu}`)
                .then(res => res.json())
                .then((coffees) => {
                    for (let i = 0; i < coffees.length; i++) {
                        //console.log(coffees[i].title)
                        items.push(
                            <CoffeeCard
                                key={i}
                                image={coffees[i].image}
                                title={coffees[i].title}
                                description={coffees[i].description}
                                price={dummyPrice}
                                handleClick={()=> {addToCard(coffees[i]) }}
                            />
                        )       
                    }    
                setCoffeeTitles(items)
            })
    },[subMenu])

    return <Container>
        <h1>POS</h1>
        <ButtonGroup aria-label="Basic Example">
            <Button variant="secondary" onClick={() => {setSubMenu('hot')}}>Hot</Button>
            <Button variant="secondary" onClick={() => {setSubMenu('iced')}}>Iced</Button>
        </ButtonGroup>
        <Row>
            <Col>
                <Row>
                    {coffeeTitles}
                </Row>
            </Col>
            <Col sm={3}>
                <h2>Cart {coffee}</h2>
                {cart.map((item, i) => {
                    return <Row key={i}>
                        <Col>{item.title}</Col>
                        <Col>{dummyPrice}</Col>
                    </Row>
                })}
                <Row>
                    Total: {cart.length * dummyPrice} Baht
                </Row>
            </Col>
        </Row>
    </Container>
}



function GradePage(){

    //Modal
    
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    //

    // Course
    //let [subCourse, setCourse] = React.useState('iced')
    let courseData = CSData


    const [coffee, setCoffee] = useLocalStorage('coffee', 'Latte')
    let [coffeeTitles, setCoffeeTitles] = React.useState([])
    let [subMenu, setSubMenu] = React.useState('iced')
    let [cart, setCart] = useLocalStorage('cart',[])

    

    React.useEffect(() => {
        let items = []
        //Run once after the page finished loading
        //Fetch from https:// api.sampleapis.com/coffee/hot
        fetch(`https://api.sampleapis.com/coffee/${subMenu}`)
            .then(res => res.json())
            .then((coffees) => {
                for (let i = 0; i < coffees.length; i++) {
                    //console.log(coffees[i].title)
                    items.push(
                        <CoffeeCard
                            key={i}
                            image={coffees[i].image}
                            title={coffees[i].title}
                            description={coffees[i].description}
                            price={dummyPrice}
                            //handleClick={()=> {addToCard(coffees[i]) }}
                        />
                    )       
                }    
            setCoffeeTitles(items)
        })
    },[subMenu])

    return (
    <>
        {/*Navation*/}
        <Navbar bg="dark" variant="dark">
            <Container>
            <Navbar.Brand href="">Low Int Sage</Navbar.Brand>
            <Nav className="me-auto">
                <Nav.Link href="">Home</Nav.Link>
                <Nav.Link href="">Grade Tracker</Nav.Link>
                <Nav.Link href="">Readme</Nav.Link>
            </Nav>
            </Container>
        </Navbar>
        <br/>

        

        {/*Modal*/}

        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
            <Modal.Title>Unselected Field</Modal.Title>
            </Modal.Header>
            <Modal.Body>Please fill all the unselected field!</Modal.Body>
            <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
                Close
            </Button>
            </Modal.Footer>
        </Modal>

        <Container>
            {/*Header*/}
            <div class="text-center mt-5 col-md-12">
                <h1>GPA Tracker</h1>
            </div>
            <ButtonGroup aria-label="Basic Example">
                        <Button variant="secondary" onClick={courseData = CSData}>CS Course</Button> {/*Set Course To CS */}
                        <Button variant="secondary" onClick={courseData = ITData}>IT Course</Button> {/*Set Course To IT */}
            </ButtonGroup>
            <br/>
            {/*Grade Content*/}
            <div class= "row mt-3 col-md-12">
                {/*Field select */}
                <div class="col-3"> 
                    
                    <h4 class="text-center">
                        Add Subject <Badge pill bg="danger">Here</Badge>
                    </h4>
                    <div class="row mt-3 col-md-12">
                        <label for="semester" class="form-label">Semester</label>
                        <div class="col-5">
                            <Form.Select id="year" aria-label="Year">
                                <option>Year</option>
                                <option value="2019">2019</option>
                                <option value="2020">2020</option>
                                <option value="2021">2021</option>
                                <option value="2021">2022</option>
                            </Form.Select>
                        </div>
                        
                        <div class="col-5">
                            <Form.Select id="term" aria-label="Term">
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                            </Form.Select>
                        </div>
                        
                    </div>
                    
                    <div class="mb-3">
                        <label for="groupSubject" class="form-label">Group Subjects</label>
                        <div class="col-12">
                            <Form.Select id="groupSubject" aria-label="Group Subject">
                                <option>--Select Group Subjects--</option>
                                {CSData.curriculum.courses.map((data, index) => {
                                    return <option key={`content_item_${index}`} value={`${data.groupName}`}>{data.groupName}</option>
                                })}
                            </Form.Select>
                        </div>
                            
                    </div>
                    
                    <div class="mb-3">
                        <label for="subject" class="form-label">Subject</label>
        
                        <div class="col-12">
                            <Form.Select id="subject" aria-label="Term">
                                <option>--Select Subject--</option>
                                {CSData.curriculum.courses[0].subjects.map((data, index) => {
                                    return <option key={`content_item_${index}`} value={`${data.name}`}> {data.code} :  {data.name}</option>
                                })}
                            </Form.Select>
                        </div>
                            
                    </div>

                    <div class="mb-3">
                        <label for="grade" class="form-label">Grade</label>
                        <div class="col-12">
                            <Form.Select id="grade" aria-label="Term">
                                <option>--Select Grade--</option>
                                <option value="A">A</option>
                                <option value="A-">A-</option>
                                <option value="B+">B+</option>
                                <option value="B">B</option>
                                <option value="B-">B-</option>
                                <option value="C+">C+</option>
                                <option value="C">C</option>
                                <option value="C-">C-</option>
                                <option value="D">D</option>
                                <option value="F">F</option>
                                <option value="W">W</option>
                            </Form.Select>
                        </div>
                    </div>
            
                </div>

                {/*Grade Table*/}
                <div class="col-9">        
                    
                    <table  id="data-table" class="table table-bordered table-striped mt-2 col-md-12"  >
                        <thead>
                            <tr class="table-dark text-center">
                                <th> </th>
                                <th>Subject ID</th>
                                <th>Subject</th>
                                <th>Grade</th>
                            </tr>
                        </thead>
                    </table>

                    <div class="row mt-3 col-md-12">
                        <div>
                            <Button variant="outline-danger" onclick="clearProduct()">
                                Clear All Subjects
                            </Button>{' '}
                        </div>
                    </div>

                </div>  
            </div>


            {/*Coffee*/}

            <ButtonGroup aria-label="Basic Example">
                <Button variant="secondary" onClick={() => {setSubMenu('CS')}}>CS Course</Button> {/*Set Course To CS */}
                <Button variant="secondary" onClick={() => {setSubMenu('IT')}}>IT Course</Button> {/*Set Course To IT */}
            </ButtonGroup>
            <Row>
                <Col>
                    <Row>
                        {coffeeTitles}
                    </Row>
                </Col>
                <Col sm={3}>
                    <h2>Cart {coffee}</h2>
                    {cart.map((item, i) => {
                        return <Row key={i}>
                            <Col>{item.title}</Col>
                            <Col>{dummyPrice}</Col>
                        </Row>
                    })}
                    <Row>
                        Total: {cart.length * dummyPrice} Baht
                    </Row>
                </Col>
            </Row>
        </Container>
    </>
    );

}





export default GradePage